import { Component, Input, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ToastrService } from 'ngx-toastr';
import { PaymentMethods } from 'shared-code-lib';
import { AuthService } from '../../../../../../../Bamba.Shared/src/services/auth/auth.service';
import { Account, AccountRegistrationDetails, Address, CreditCard } from '../../../blocks/InterFAX/Models/admin';
import { Log } from '../../../blocks/core/log';
import { ErrorsService } from '../../../blocks/services/errors.service';
import { InterFAXService } from '../../../blocks/services/interfax.service';
import { ApiResult, Control } from '../../../blocks/services/models';
import { SpinnerStateService } from '../../../blocks/services/spinner-state.service';
import { UserStateService } from '../../../blocks/services/user-state.service';
import { PaymentMethodConfigurations } from '../../../blocks/InterFAX/Models/payment-methods';
import { KeyValue } from '@angular/common';

@Component({
  selector: 'app-payment-method',
  templateUrl: './account-payment-method.component.html',
  styleUrls: ['./account-payment-method.component.scss'],
})
export class PaymentMethodComponent implements OnInit {
  @Input() account: Account;
  @Input() accountRegistrationDetails: AccountRegistrationDetails;

  readonly REDIRECT_URL: string = 'REDIRECT_URL';
  readonly RETURN_URL: string = 'RETURN_URL';
  creditCard = new CreditCard();
  paymentMeans: string = PaymentMethods.None;
  currentPaymentMeans: string = PaymentMethods.None;
  updatePayment = false;
  isCreditEnabled: boolean;
  isDirectDebitEnabled: boolean;
  isDirectDebitConfigured: boolean = false;
  isPayPalEnabled: boolean;
  updatedAddress: Address;
  updatedCreditCard: CreditCard;

  rightControls: Array<Control> = [];

  PaymentMethods = PaymentMethods;

  showEditCCForm = false;

  config: PaymentMethodConfigurations;

  constructor(
    private route: ActivatedRoute,
    private interfaxService: InterFAXService,
    private toastrService: ToastrService,
    private errorsService: ErrorsService,
    private spinnerService: SpinnerStateService,
    private translate: TranslateService,
    private router: Router,
    private userStateService: UserStateService,
    private authService: AuthService,
    private interFAXService: InterFAXService,
    private errorService: ErrorsService
  ) {}

  ngOnInit(): void {
    this.createButtons();

    this.spinnerService.show(this.translate.instant('Common.Loading'), document.documentElement);
    this.interfaxService.getAccount(this.accountLoaded, this.loadFailed);
  }

  private createButtons(): void {
    const saveButton = new Control(this.onSave, 'Save', 'btn btn-primary btn-minwidth', '', '', 'btn', false);
    const cancelButton = new Control(this.close, 'Cancel', 'btn btn-light btn-minwidth', '', '', 'btn', false);
    this.rightControls.push(saveButton);
    this.rightControls.push(cancelButton);

    saveButton.setTranslation(this.translate.instant('Common.Save'));
    cancelButton.setTranslation(this.translate.instant('Common.Cancel'));
  }

  loadFailed = (err: any): void => {
    Log.error('PaymentMethodComponent.loadFailed', err);
    this.toastrService.error(this.translate.instant('fax-user-properties.account-load-failed'));
    this.spinnerService.hide();
  };

  private accountLoaded = (account: Account): void => {
    this.account = account;
    this.currentPaymentMeans = account.paymentMeans;
    this.config = new PaymentMethodConfigurations(this.currentPaymentMeans);

    this.interfaxService.getAccountRegistrationDefaults(
      this.account.shippingSameAsBilling ? this.account.billing.country : this.account.shipping.country,
      this.accountRegistrationDetailsLoaded,
      this.loadFailed
    );
  };

  accountRegistrationDetailsLoaded = (details: AccountRegistrationDetails): void => {
    this.accountRegistrationDetails = details;
    this.spinnerService.hide();

    this.initializePaymentMeans();
  };

  initializePaymentMeans = (): void => {
    this.isCreditEnabled = this.accountRegistrationDetails.country.paymentMeans.includes(PaymentMethods.CC);
    this.isDirectDebitEnabled = this.accountRegistrationDetails.country.paymentMeans.includes(PaymentMethods.DD);
    this.isPayPalEnabled = this.accountRegistrationDetails.country.paymentMeans.includes(PaymentMethods.PayPal);
    this.paymentMeans = this.account?.paymentMeans;
    this.account.isCreditCardOwnerIdRequired = this.accountRegistrationDetails.country.ownerIDRequired;
    this.isDirectDebitConfigured = this.account.paymentMeans === PaymentMethods.DD;

    const initialPaymentMeans = this.route.snapshot.queryParamMap.get('payment-method');
    if (!initialPaymentMeans) return;

    this.selectPaymentMeans(PaymentMethods[initialPaymentMeans as keyof typeof PaymentMethods]);
  };

  selectPaymentMeans = (value: PaymentMethods): void => {
    this.paymentMeans = this.account.paymentMeans = value;
  };

  onUpdatePayment = (value: boolean): void => {
    if (!this.account?.creditCard) return;

    this.account.creditCard.changed = value;
  };

  onAddressChanged = (address: Address): void => {
    this.updatedAddress = address;
  };

  onCreditCardChanged = (creditCard: CreditCard): void => {
    this.updatedCreditCard = creditCard;
  };

  onSave = (): void => {
    if (this.isDirectDebitSelected()) {
      this.toastrService.error(this.translate.instant('payment-method.direct-debit-not-configured'));
      return;
    }

    if (this.isCreditCardInfoNotChanged()) {
      this.saveSucceeded();
      return;
    }

    let account = new Account(this.account);

    if (this.paymentMeans === PaymentMethods.CC) {
      account.creditCard = this.updatedCreditCard;
      account.billing = this.updatedAddress;
    }

    this.spinnerService.show(this.translate.instant('Common.Saving'), document.documentElement);
    this.interfaxService.setAccount(account, true, this.saved, this.saveFailed);
  };

  isDirectDebitSelected(): boolean {
    return !this.isDirectDebitConfigured && this.account.paymentMeans === PaymentMethods.DD;
  }

  isCreditCardInfoNotChanged(): boolean {
    return this.account.paymentMeans === PaymentMethods.CC && !this.showEditCCForm;
  }

  saved = (result: ApiResult): void => {
    this.spinnerService.hide();

    if (result.success && result.object?.redirectUrl) {
      this.authService.shareSessionForTabs(60 * 1000);
      location = result.object.redirectUrl;
      return;
    }

    if (result.success) {
      this.saveSucceeded();
      return;
    }

    this.errorsService.showErrorsModal(result.errors, 'payment-method.save-fail');
  };

  saveSucceeded(): void {
    this.toastrService.success(this.translate.instant('payment-method.save-success'));
    this.close();
  }

  saveFailed = (): void => {
    this.spinnerService.hide();
    this.toastrService.error(this.translate.instant('payment-method.save-fail'));
  };

  close = (): void => {
    this.router.navigateByUrl(this.userStateService.getUserHomePage());
  };

  onSetupCompleted = (paymentMeans: string): void => {
    this.account.paymentMeans = paymentMeans;
  };

  startSetup = (config: PaymentMethodConfigurations): void => {
    this.config = config;

    this.spinnerService.show(
      this.translate.instant(`fax-account-properties.start-${config.name.toLocaleLowerCase()}-setup`),
      document.documentElement
    );

    const url = `${window.location.protocol}//${window.location.host}/payment-method-callback?payment-method=${config.name}`;
    config.configurations.push({ key: this.RETURN_URL, value: url });

    this.interFAXService.startPaymentMethodSetup(config, this.onSetupStartSucceeded, this.onSetupStartFailed);
  };

  onSetupStartSucceeded = (result: KeyValue<string, string>[]): void => {
    this.spinnerService.hide();

    const redirectURL = result.find((x) => x.key == this.REDIRECT_URL)?.value;
    if (!redirectURL) return;

    this.authService.shareSessionForTabs();
    window.location.href = redirectURL;
  };

  onSetupStartFailed = (err: any): void => {
    this.spinnerService.hide();

    this.errorService.showErrorsModal(
      err,
      this.translate.instant(
        `fax-account-properties.${
          this.config?.name.toLocaleLowerCase() ?? this.currentPaymentMeans?.toLocaleLowerCase()
        }-setup-failed`
      )
    );
  };
}
