import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';

import { FormsModule } from '@angular/forms';
import { RouterModule, Routes } from '@angular/router';
import { TranslateModule } from '@ngx-translate/core';
import { PerfectScrollbarModule } from 'ngx-perfect-scrollbar-portable';
import { NgxSmartModalModule } from 'ngx-smart-modal';
import {
  AutoFocusFirstInputModule,
  AutoUpdatePerfectScrollModule,
  CheckboxModule,
  FieldsModule,
  IconsModule,
  LabelModule,
  ModalHeaderModule,
  RadioButtonsGroupModule,
  TabBarModule,
} from 'shared-code-lib';
import { ComponentsModule } from '../../../blocks/components/components.module';
import { ModalsModule } from '../../../blocks/modals/modals.module';
import { PageModule } from '../../../blocks/page/page.module';
import { AccountComponentsModule } from '../account-components/account-components.module';
import { PaymentMeansModule } from '../payment-means/payment-means.module';
import { PaymentMethodComponent } from './account-payment-method.component';

const routes: Routes = [
  {
    path: '',
    component: PaymentMethodComponent,
    data: { type: 'properties' },
    pathMatch: 'full',
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class AccountPaymentMeansRoutingModule {}

@NgModule({
  declarations: [PaymentMethodComponent],
  imports: [
    CommonModule,
    TranslateModule,
    PageModule,
    ComponentsModule,
    FormsModule,
    PerfectScrollbarModule,
    IconsModule,
    FieldsModule,
    AutoFocusFirstInputModule,
    TabBarModule,
    CheckboxModule,
    NgxSmartModalModule,
    ModalHeaderModule,
    LabelModule,
    AccountPaymentMeansRoutingModule,
    PaymentMeansModule,
    RadioButtonsGroupModule,
    AccountComponentsModule,
    ModalsModule,
    AutoUpdatePerfectScrollModule,
  ],
  exports: [],
})
export class AccountPaymentMethodModule {}
